import "./styles.scss";
import { useState } from "react";
// import "bootstrap/dist/css/bootstrap.min.css";

function getDestinyNumber(numberString = "") {
  if (parseInt(numberString, 10) < 10) {
    return numberString;
  }
  let total = 0;
  try {
    for (let digit of numberString) {
      let parseNumber = parseInt(digit, 10);
      console.log("parseNum", parseNumber);
      if (isNaN(parseNumber)) {
        console.error(`contains NaN letter: (${digit})`);
        return 0;
      }
      total += parseNumber;
    }
  } catch (e) {
    console.log("numberString", numberString);
    console.error(e);
  }

  return getDestinyNumber(total.toString());
}

// console.log(getDestinyNumber("l"));
console.log(getDestinyNumber("1998103"), 1 + 9 + 9 + 8 + 1 + 0 + 3);

